// Mixins

// Media queries
@mixin mQ($args...) {
    @if length($args) == 1 {
       @media screen and (max-width: nth($args, 1)) {
        @content;
    }
 }
 @if length($args) == 2 {
    @media screen and (max-width: nth($args, 1)) and (min-width: nth($args, 2)) {
        @content;
    }
 }
}
// headline for desktops
@mixin headline {
    font-family: 'Palanquin Dark', sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 57px;
    line-height: 10px;
    text-align: left;
    text-transform: capitalize;
    color: white;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

// paragraph text for desktops
@mixin paragraph {
    // font-family: 'Roboto';
    font-family: 'Rubik', sans-serif;
    font-style: normal;
    font-weight: 300;
    font-size: 25px;
    line-height: 128.69%;
    letter-spacing: 0.025em;
    color: #efefef;
    text-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
}

// character
@mixin characters($background, $color, $character, $width, $height, $direction) {
    position: relative;
    background: $color;
    width: 100%;
    height: 800px;
    display: flex;
    flex-direction: $direction;
    justify-content: space-evenly;
    align-items: center;
    overflow: hidden;
    

    @include mQ(450px) {
        height: 844px;
        flex-direction: column;
        justify-content: space-evenly;
    }

    @include mQ(650px, 450px) {
        height: 844px;
        flex-direction: column;
        justify-content: space-evenly;
    }
    .img-container {
        z-index: 0;
        background: url($character);
        width: $width;
        height: $height;
        background-size: cover;
        background-repeat: no-repeat;

      @include floating(6s, 0s, 12px);

        @include mQ(1000px, 450px) {
            width: ($width / 1.2);
            height: ($height / 1.2);
        }
        @include mQ(450px) {
            width: ($width / 1.5);
            height: ($height / 1.5);
        }
    }
    .opacityimg {
        @include opacity(.5s, 1.3s) 
       }
       
    .text-container {
        z-index: 1;
        width: 550px;
        @include mQ(1000px, 450px) {
            width: 40%;
        }

        @include mQ(650px, 450px) {
            width: 70%;
            text-align: center;
        }

        @include mQ(450px) {
            width: 80%;
        }
        h1 {
            @include headline;

            @include mQ(650px, 450px) {
                text-align: center;
            }

            @include mQ(450px) {
                font-weight: 500;
                font-size: 33px;
            }
            @include mQ(1000px, 450px) {
                font-weight: 500;
                font-size: 45px;
                line-height: 90%;
            }
        }
        p {
            @include paragraph;
            @include mQ(450px) {
                font-weight: 400;
                font-size: 18px;
                line-height: 128.69%;
            }
            @include mQ(1000px, 450px) {
                font-weight: 500;
                font-size: 24px;
            }
        }
    }
    #cover {
        background: url($background);
        width: 100%;
        height: 100%;
        position: absolute;
        background-size: cover;
        background-repeat: no-repeat;
        z-index: 0;
        bottom: 0px;
        @include mQ(450px) {
            height: 27%;
        }
    }
}


// Face reveal animations
// TranslateX, width, height, unopacity, scale
@mixin unopacity($time, $delay){
    animation: unopacity $time $delay cubic-bezier(0.77, 0, 0.175, 1) both;
    transition: .3s;
    @keyframes unopacity {
        from {
        }
        to {
            opacity: 0;
            z-index: 0;
        }
    }
}

@mixin translatex($time, $delay){
    animation: translatex $time $delay cubic-bezier(0.77, 0, 0.175, 1) both;
    transition: .3s;
    @keyframes translatex {
        from {
        }
        to {
            transform:translateX(100%);
            z-index: 0;
        }
    }
}

@mixin translateminus($time, $delay){
    animation: translateminus $time $delay cubic-bezier(0.77, 0, 0.175, 1) both;
    transition: .3s;
    @keyframes translateminus {
        from {
        }
        to {
            transform:translateX(-100%);
            z-index: 0;
        }
    }
}

@mixin scale($time, $delay){
    animation: scale $time $delay cubic-bezier(0.77, 0, 0.175, 1) both;
    transition: .3s;
    @keyframes scale {
        from {
        }
        to {
            width: 500%;
            opacity: 0;
            z-index: 0;
        }
    }
}

@mixin width($time, $delay){
    animation: width $time $delay cubic-bezier(0.77, 0, 0.175, 1) both;
    transition: .3s;
    @keyframes width {
        from {
        }
        to {
            transform: scaleX(0);
            z-index: 0;
        }
    }
}

@mixin height($time, $delay){
    animation: height $time $delay cubic-bezier(0.77, 0, 0.175, 1) both;
    transition: .3s;
    @keyframes height {
        0% {
        }
        100% {
            transform: scaleY(0);
            z-index: 0;
        }
    }
}

@mixin stretch($time, $delay){
    animation: stretch $time $delay cubic-bezier(0.77, 0, 0.175, 1) both;
    transition: .3s;
    @keyframes stretch {
        0% {
        }
        100% {
            transform: scaleX(6);
            opacity: 0;
            z-index: 0;
        }
    }
}

@mixin shake($time, $delay){
    animation: shake $time $delay cubic-bezier(0.77, 0, 0.175, 1) both;
    transition: .3s;
    @keyframes shake {
        0% {
        }
        90% {
            transform:translateX(100%)
             rotate(180deg);
        }
        100% {
            transform:translateX(100%)
             rotate(180deg);
            z-index: 0;
        }
    }
}
// Face creator
@mixin face($color, $image, $width) {
    .face-background {
    background-color: $color;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 2;
    display: flex;
    justify-content: center;
    align-items: center;
    
    .face {
    width: $width;
    height: inherit;
    background: url($image);
    background-size: cover;
    background-repeat: no-repeat;
  }
  }
}

// Clouds maker
@mixin clouds($image, $width, $height, $top, $left) {
        position: absolute;
        background: url($image);
        background-size: cover;
        background-repeat: no-repeat;
        height: $height;
        width: $width;
        left: $left;
        top: $top;    
}

// Objects Animations
@mixin floating($time, $delay, $y){
    animation: floating $time $delay ease-in-out infinite;
    transition: .3s;
    @keyframes floating {
        0% {
            transform: translateY($y);
        }
        50% {
            transform: translateY(-$y);
        }
        100% {
            transform: translateY($y);
        }
    }
}

@mixin floatingx($time, $delay, $y, $rotation){
    animation: floatingx $time $delay ease-in-out infinite;
    transition: .3s;
    @keyframes floatingx {
        0% {
            transform: translateY($y);
        }
        50% {
            transform: translateY(-$y) rotate($rotation);
        }
        100% {
            transform: translateY($y);
        }
    }
}

@mixin wind($time, $delay, $X){
    animation: wind $time $delay ease-in-out infinite;
    transition: .3s;
    @keyframes wind {
        0% {
            transform: translateX($X);
        }
        50% {
            transform: translateX($X * 2);
        }
        100% {
            transform: translateX($X);
        }
    }
}

@mixin opacity($time, $delay){
    opacity: 0;
    animation: opacity $time $delay cubic-bezier(0.77, 0, 0.175, 1) forwards;
    @keyframes opacity {
        0% {
            opacity: 0;
        }
        100% {
            opacity: 1;
        }
    }
}

